* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
html,
body,
#root {
	min-width: fit-content;
	min-height: 100vh;
	// width: fit-content;
	height: fit-content;
}

::-webkit-scrollbar {
	width: 0px;
	height: 0px;
	background: palevioletred; /* make scrollbar transparent */
}
